.skill_card{
    background: rgba(0,0, 0, 0.1);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;


}

.skill-box .title{
    display: block;
    font-size: 14px;
    font-weight: 600;
    color: #333;
}

.skill-box .skill-bar{
    height: 8px;
    width: 100%;
    border-radius: 6px;
    margin-top: 6px;
    background: rgba(0,0, 0, 0.1);
}

.skill-box .skill-per{
    position: relative;
    display: block;
    height: 100%;
    width: 70%;
    border-radius: 6px;
    background: #f74252;
    animation: progress 0.4s ease-in-out forwards;
    opacity: 0;
}

.skill-per.fifty {
    width: 50%;
    animation-delay: 0.1s;
   
}

.skill-per.sixty{
    width: 60%;
    animation-delay: 0.2s;
}

@keyframes progress {   
    0% {
        width: 0;
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

.skill-per .tooltip{
    position: absolute;
    right: -14px;
    top: -28px;
    font-size: 9px;
    font-weight: 500;
    color: #fff;
    padding: 2px 6px;
    border-radius: 3px;
    background: #f74252;
    z-index: 1;
}

.tooltip::before {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -2px;
    height: 10px;
    width: 10px;
    z-index: -1;
    background-color: #f74252;
    transform: translateX(-50%) rotate(45deg);
}

.getTO{
    color: #fff;
padding-top: 50px;
}